:root{
   --primary :#e74c3c;
   --secondary: #CAD3C8;
   --dim-font: #576574; 
}
header{
  background: var(--primary);
  color: white !important;

}

.Footer{
  background-color: #61dafb;

}
input, textarea{
  border: none;
 

}
button{
  color: white !important;
}
.container{
  /* background: yellow; */
}
.CreateNote{
  /* background: #f1f1f1; */
  /* background: green !important; */
  width: 500px;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  /* height: 300px; */
  border-radius: 10px;
  box-shadow: 0 1px 7px rgb(128 128 128);
}

.CreateNote  input{
  /* position: absolute; */
  padding: 1rem ;
  font-size: 1.6rem;
  border-radius: 10px;

}
.CreateNote  textarea{
  /* width: 100%; */
  padding: 1rem 2rem;
  resize: none;
  font-size: 1.4rem;
  color: black;
  background: none;
  background: #f1f1f0;
}
.CreateNote button{
  position: absolute;
  bottom: -10px;
  right: 5px;
  /* width: 40px; */
  border-radius: 50%;
  background: var(--primary);
  
}
.CreateNote input{
  border-bottom: 1px solid var(--primary);
}
.notes_container{
  /* background: yellow; */
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  /* flex-direction: row; */
}
.Note{
  position: relative;
  width: 22rem;
  /* background: #4365aa; */
  /* text-align: left;/ */
  /* background: var(--secondary); */

  padding: .2rem .4rem;
  border-radius: 7px;
  box-shadow: 0 1px 7px rgb(128 128 128);
  margin: 2rem;
}
.Note button{
  position: absolute;
  right: 5px;
  bottom: 0px;
  font-size: .6rem;
  background: #F97F51;
  padding: .2rem .4rem;
  border-radius: 50%;
  border: none;
}
.Note input{
  padding: 2rem;
}